<template>
    <el-row>
        <el-col :span="6" :offset="9">
            <img src="static/img/404.jpg" v-if="!loading">
            <i class="el-icon-loading" style="font-size:72px;margin-top:50px;margin-left:100px;" v-if="loading"></i>
        </el-col>
        <el-col :span="4">
            <el-button style="margin-top:60px;" title="返回上一页" icon="el-icon-back" circle @click="backUrl"></el-button>
        </el-col>
    </el-row>
</template>
<script>
export default {
    data() {
        return {
            loading: true,
            listfuncInfoDto: []
        };
    },
    created() {
        let userallInfo = JSON.parse(sessionStorage.getItem("zsylocalInfo"));
        if (userallInfo) {
            this.listfuncInfoDto = userallInfo.data.functionDtoList;
            var childlst = [];
            const _import = require("@/router/_import_" + process.env.NODE_ENV);
            var menuItems = this.listfuncInfoDto;
            for (var item in menuItems) {
                if (menuItems[item].remarks) {
                    childlst.push({
                        path: menuItems[item].code,
                        component: _import(menuItems[item].remarks)
                    });
                }
                var subItems = menuItems[item].children;
                for (var subItem in subItems) {
                    if (subItems[subItem].remarks) {
                        childlst.push({
                            path: subItems[subItem].code,
                            component: _import(subItems[subItem].remarks)
                        });
                    }
                }
            }
            var routerlst = [
                {
                    path: "/default",
                    component: _import("core/home/Default"),
                    children: childlst
                }
            ];
            this.$router.addRoutes(routerlst);
            var self = this;
            setTimeout(function() {
                self.loading = false;
            }, 3000);
        }
    },
    mounted() {
        this.loading = true;
    },
    methods: {
        backUrl() {
            this.$router.go(-1);
        }
    }
};
</script>



